.header__auth {
	display: flex;
	flex-direction: column;
	align-items: flex-end;
	margin: 0 30px 0;
	max-width: 126px;
	overflow: hidden;
	text-overflow: ellipsis;
	white-space: nowrap;
}

@media screen and (min-width: 768px) {
	.header__auth {
		margin: 0;
		flex-direction: row;
		align-items: center;
		gap: 24px;
		max-width: 250px;
	}
}